// Phase 74: Billing Analytics Dashboard
'use client';
import { useEffect, useState } from 'react';

export default function BillingPage() {
  const [data, setData] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/billing/summary')
      .then(res => res.json())
      .then(data => {
        setData(data);
        setLoading(false);
      })
      .catch(err => {
        console.error(err);
        setLoading(false);
      });
  }, []);

  if (loading) return <div style={{ padding: '2rem' }}>Loading...</div>;

  return (
    <div style={{ maxWidth: '1200px', margin: '2rem auto', padding: '2rem' }}>
      <h1>Billing Analytics</h1>
      
      <div style={{
        display: 'grid',
        gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
        gap: '1rem',
        marginTop: '2rem'
      }}>
        <div style={{
          padding: '1.5rem',
          backgroundColor: '#f5f5f5',
          borderRadius: '8px',
          border: '1px solid #ddd'
        }}>
          <h3 style={{ margin: 0, fontSize: '0.875rem', color: '#666' }}>MRR</h3>
          <p style={{ margin: '0.5rem 0 0', fontSize: '2rem', fontWeight: 'bold' }}>
            ${data?.mrr || 0}
          </p>
        </div>
        
        <div style={{
          padding: '1.5rem',
          backgroundColor: '#f5f5f5',
          borderRadius: '8px',
          border: '1px solid #ddd'
        }}>
          <h3 style={{ margin: 0, fontSize: '0.875rem', color: '#666' }}>ARPA</h3>
          <p style={{ margin: '0.5rem 0 0', fontSize: '2rem', fontWeight: 'bold' }}>
            ${data?.arpa || 0}
          </p>
        </div>
        
        <div style={{
          padding: '1.5rem',
          backgroundColor: '#f5f5f5',
          borderRadius: '8px',
          border: '1px solid #ddd'
        }}>
          <h3 style={{ margin: 0, fontSize: '0.875rem', color: '#666' }}>Customers</h3>
          <p style={{ margin: '0.5rem 0 0', fontSize: '2rem', fontWeight: 'bold' }}>
            {data?.customers || 0}
          </p>
        </div>
        
        <div style={{
          padding: '1.5rem',
          backgroundColor: '#f5f5f5',
          borderRadius: '8px',
          border: '1px solid #ddd'
        }}>
          <h3 style={{ margin: 0, fontSize: '0.875rem', color: '#666' }}>Churn</h3>
          <p style={{ margin: '0.5rem 0 0', fontSize: '2rem', fontWeight: 'bold' }}>
            {data?.churn || 0}%
          </p>
        </div>
      </div>
      
      <div style={{ marginTop: '3rem' }}>
        <h2>Revenue Breakdown</h2>
        <table style={{
          width: '100%',
          marginTop: '1rem',
          borderCollapse: 'collapse'
        }}>
          <thead>
            <tr style={{ backgroundColor: '#f5f5f5' }}>
              <th style={{ padding: '0.75rem', textAlign: 'left', borderBottom: '2px solid #ddd' }}>
                Metric
              </th>
              <th style={{ padding: '0.75rem', textAlign: 'right', borderBottom: '2px solid #ddd' }}>
                Value
              </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td style={{ padding: '0.75rem', borderBottom: '1px solid #eee' }}>
                Monthly Recurring Revenue
              </td>
              <td style={{ padding: '0.75rem', textAlign: 'right', borderBottom: '1px solid #eee' }}>
                ${data?.mrr || 0}
              </td>
            </tr>
            <tr>
              <td style={{ padding: '0.75rem', borderBottom: '1px solid #eee' }}>
                Annual Run Rate (ARR)
              </td>
              <td style={{ padding: '0.75rem', textAlign: 'right', borderBottom: '1px solid #eee' }}>
                ${((data?.mrr || 0) * 12).toFixed(2)}
              </td>
            </tr>
            <tr>
              <td style={{ padding: '0.75rem', borderBottom: '1px solid #eee' }}>
                Average Revenue Per Account
              </td>
              <td style={{ padding: '0.75rem', textAlign: 'right', borderBottom: '1px solid #eee' }}>
                ${data?.arpa || 0}
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
}
